header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 32px;
    padding-right: 32px;
    h1{
        color:$black;
        font-size: 24px;
        font-weight: bold;
        margin-right: 50px;
        line-height: 33px;
    }
    h2{
        color:$black;
        font-size: 16px;
        font-weight: bold;
        line-height: 1.5;
        cursor:pointer;
    }
    .head-title{
        display: flex;
        align-items: center;
        height: 93px;
    }
    .head-icon{
        i{
            margin-right: 26px;
            cursor:pointer;
        }
    }
}
.arrow{
    font-weight: bold;
    font-size: 24px;
    height: 24px;
    color:$green;
    margin-left: 5px;
}
.plus{
    font-weight: bold;
    font-size: 24px;
    padding-bottom: 2px;
    color:$green;
    margin-right: 10px;
}